@font-face {
  font-family: "Rubik";
  src: url("../font/Rubik-Light.woff2") format("woff2"), url("../font/Rubik-Light.woff") format("woff");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Rubik";
  src: url("../font/Rubik-Regular.woff2") format("woff2"), url("../font/Rubik-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Rubik";
  src: url("../font/Rubik-Medium.woff2") format("woff2"), url("../font/Rubik-Medium.woff") format("woff");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Rubik";
  src: url("../font/Rubik-Bold.woff2") format("woff2"), url("../font/Rubik-Bold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

ul,
ol {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote {
  margin: 0;
}

ul,
ol {
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
}

textarea {
  resize: none;
}

input,
button,
textarea,
select {
  font: inherit;
}

a {
  text-decoration: none;
  color: inherit;
}

html,
body {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
}

.main {
  flex-grow: 1;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

a,
input,
button {
  outline: 0;
  border: 0;
}

button {
  background-color: transparent;
  cursor: pointer;
}

/*---------------------------------*/

::-webkit-scrollbar {
  width: 0;
}

*::-webkit-scrollbar {
  width: 2px;
  height: 2px;
}

*::-webkit-scrollbar-track {
  background-color: transparent;
}

*::-webkit-scrollbar-thumb {
  background-color: #ff6838;
}

.container {
  max-width: 1200px;
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

body {
  font-family: "Rubik", "Helvetica", "Arial", sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 1.5;
  color: #31352b;
  background-color: #f9faff;
}

.logo {
  display: inline-block;
  border-radius: 5px;
  transition: opacity 0.3s, box-shadow 0.3s ease-in-out;
}

.logo:hover {
  opacity: 0.8;
}

.logo:focus {
  box-shadow: 0 0 0 2px rgba(255, 104, 56, 0.7019607843);
}

.logo:active {
  box-shadow: 0 0 0 2px transparent;
  opacity: 0.5;
}

.logo__img {
  width: 200px;
  height: 39px;
}

.link {
  border-radius: 5px;
  transition: color 0.3s, box-shadow 0.3s, text-shadow 0.3s ease-in-out;
}

.link:hover {
  color: #ff6838;
}

.link:focus {
  box-shadow: 0 0 0 2px rgba(255, 104, 56, 0.7019607843);
}

.link:active {
  box-shadow: 0 0 0 2px transparent;
  text-shadow: 2px 2px 5px rgba(49, 54, 43, 0.5019607843);
}

.link--active {
  color: #ff6838;
}

.title {
  font-weight: 500;
  line-height: 1.3;
  font-size: 36px;
  text-align: center;
  color: #363853;
}

.title--span {
  display: block;
  text-align: left;
}

.btn-bg {
  padding: 0;
  line-height: 1;
  color: #fff;
  border-radius: 5px;
  background-color: #ff6838;
  transition: color 0.3s, box-shadow 0.3s, background-color 0.3s ease-in-out;
}

.btn-bg:hover {
  color: #ff6838;
  background-color: rgba(255, 255, 255, 0.8901960784);
  box-shadow: 0 5px 12px rgba(255, 103, 56, 0.2), 0 0 0 2px rgba(255, 104, 56, 0.7019607843);
}

.btn-bg:focus {
  color: #ff6838;
  background-color: rgba(255, 122, 82, 0.1019607843);
  box-shadow: 0 0 0 2px rgba(255, 104, 56, 0.7019607843);
}

.btn-bg:active {
  color: #ff6838;
  background-color: rgba(255, 122, 80, 0.1019607843);
  box-shadow: 0 0 0 2px transparent;
}

.popular,
.restaurant,
.apps {
  padding: 120px 0;
}

/*---------------------------------*/

.lock {
  overflow: hidden;
  position: relative;
}

body.header-fixed header {
  position: fixed;
  top: 0;
  right: 0;
  padding-top: 0;
  width: 100%;
  z-index: 1;
}

.header {
  padding-top: 57px;
  width: 100%;
  position: fixed;
  background-color: #f9faff;
  transition: 0.2s all;
}

.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.navbar__logo {
  margin-left: 5px;
}

.navbar__inner {
  display: flex;
  align-items: center;
}

.menu {
  width: 301px;
  display: flex;
  justify-content: space-between;
  margin-right: 50px;
}

.menu__link {
  font-size: 18px;
  line-height: 1.1666666667;
}

.user {
  display: flex;
}

.user__item:nth-child(1) {
  margin-right: 10px;
}

.user__item:nth-child(2) {
  position: relative;
}

.user__btn {
  width: 44px;
  height: 44px;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: background-color 0.3s, box-shadow 0.3s ease-in-out;
}

.user__btn:hover {
  background-color: #ff6838;
}

.user__btn:focus {
  background-color: rgba(255, 122, 80, 0.1019607843);
  box-shadow: 0 0 0 2px inset rgba(255, 104, 56, 0.7019607843);
}

.user__btn:active {
  background-color: #fff2ed;
  box-shadow: 0 0 0 2px transparent;
}

.user__btn:hover .user__icon {
  stroke: #fff;
}

.user__btn:hover .user__count {
  color: #ff6838;
  background-color: #fff;
}

.user__btn:focus .user__icon {
  stroke: #363853;
}

.user__btn:focus .user__count {
  color: #fff;
  background-color: #ff6838;
}

.user__icon {
  stroke: #363853;
  transition: stroke 0.3s ease-in-out;
}

.user__count {
  position: absolute;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  font-size: 10px;
  line-height: 1.2;
  color: #fff;
  background-color: #ff6838;
  top: 6px;
  right: 6px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: color 0.3s, background-color 0.3s ease-in-out;
}

.burger {
  padding: 0;
  position: relative;
  width: 44px;
  height: 44px;
  border-radius: 5px;
  margin: 0 2px 0 10px;
  display: none;
  transition: background-color 0.3s, box-shadow 0.3s ease-in-out;
}

.burger:hover {
  background-color: #ff6838;
}

.burger:hover .burger__line {
  background-color: #fff;
}

.burger:hover .burger__line::before,
.burger:hover .burger__line::after {
  background-color: #fff;
}

.burger:focus {
  background-color: rgba(255, 122, 80, 0.1019607843);
  box-shadow: 0 0 0 2px inset rgba(255, 104, 56, 0.7019607843);
}

.burger:focus .burger__line {
  background-color: #363853;
}

.burger:focus .burger__line::before,
.burger:focus .burger__line::after {
  background-color: #363853;
}

.burger:active {
  background-color: #fff2ed;
  box-shadow: 0 0 0 2px transparent;
}

.burger__line {
  position: absolute;
  content: "";
  width: 30px;
  height: 2px;
  margin: auto;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: #363853;
  transition: background-color 0.3s ease-in-out;
}

.burger__line::before {
  position: absolute;
  content: "";
  width: 30px;
  height: 2px;
  top: -10px;
  left: 0;
  background-color: #363853;
  transition: background-color 0.3s ease-in-out;
}

.burger__line::after {
  position: absolute;
  content: "";
  width: 30px;
  height: 2px;
  left: 0;
  bottom: -10px;
  background-color: #363853;
  transition: background-color 0.3s ease-in-out;
}

.burger-menu {
  display: block;
  position: absolute;
  width: 300px;
  height: 100vh;
  top: 0;
  left: 0;
  background-color: #fff;
  transform: translateX(-1000%);
  transition: transform 0.3s ease-in-out;
}

.burger-menu__top {
  padding: 25px 26px 25px 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #eeeff3;
}

.burger-menu__close {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2px;
  width: 20px;
  height: 20px;
  border-radius: 5px;
  transition: background-color 0.3s, box-shadow 0.3s ease-in-out;
}

.burger-menu__close:hover {
  background-color: #ff6838;
}

.burger-menu__close:hover .burger-menu__icon {
  fill: #fff;
}

.burger-menu__close:focus {
  background-color: rgba(255, 122, 80, 0.1019607843);
  box-shadow: 0 0 0 2px rgba(255, 104, 56, 0.7019607843);
}

.burger-menu__close:focus .burger-menu__icon {
  fill: #363853;
}

.burger-menu__close:active {
  background-color: #fff2ed;
  box-shadow: 0 0 0 2px transparent;
}

.burger-menu__icon {
  width: 18px;
  height: 18px;
  fill: #363853;
  transition: fill 0.3s ease-in-out;
}

.burger-menu .menu {
  display: block;
}

.burger-menu--active {
  transform: translateX(0%);
}

.hero {
  padding-top: 160px;
}

.hero__inner {
  min-height: 660px;
  background-repeat: no-repeat;
  background-position: right;
  background-size: contain;
  display: flex;
  align-items: center;
}

.hero__title {
  font-size: 66px;
  font-weight: 500;
  line-height: 1.3;
  color: #363853;
  text-transform: uppercase;
  margin-bottom: 20px;
}

.hero__title-span {
  display: block;
  color: #ff6838;
}

.hero__text {
  max-width: 450px;
  margin-bottom: 20px;
}

.hero__button {
  padding: 12px 31px;
  font-size: 18px;
  line-height: 1.3;
  letter-spacing: 0.03em;
  margin-right: 30px;
}

.hero__link {
  color: #363853;
}

.popular {
  text-align: center;
}

.popular__title {
  padding-left: 5px;
  margin-bottom: 60px;
}

.popular__filter {
  display: flex;
  overflow: auto;
  padding: 2px;
  margin-bottom: 42px;
}

.popular__item:not(:last-child) {
  margin-right: 20px;
}

.popular__button {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #363853;
  padding: 0;
  white-space: nowrap;
  border-radius: 5px;
  padding: 12px 32px;
  box-shadow: 0 0 0 1px #ececec;
  background-color: #fff;
  transition: color 0.3s, box-shadow 0.3s, background-color 0.3s ease-in-out;
}

.popular__button:hover {
  color: #ff6838;
  background-color: rgba(255, 122, 80, 0.1019607843);
  box-shadow: 0 0 0 2px rgba(255, 104, 56, 0.7019607843);
}

.popular__button:hover .popular__icon {
  fill: #ff6838;
}

.popular__button:focus {
  color: #363853;
  background-color: #fff;
  box-shadow: 0 0 0 2px rgba(255, 104, 56, 0.7019607843);
}

.popular__button:focus .popular__icon {
  fill: #363853;
}

.popular__button:active {
  color: #363853;
  box-shadow: inset 0 -5px 12px rgba(255, 103, 56, 0.2);
}

.popular__button:active .popular__icon {
  fill: #363853;
}

.popular__button--active {
  color: #fff;
  background-color: #ff6838;
}

.popular__button--active .popular__icon {
  fill: #fff;
}

.popular__icon {
  fill: #363853;
  margin-right: 10px;
  transition: fill 0.3s ease-in-out;
}

.popular__link {
  font-weight: 400;
  font-size: 20px;
  line-height: 1;
  padding: 0;
}

.popular-food {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 30px;
  margin-bottom: 61px;
}

.popular-food__item {
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0 0 0 1px #ececec;
  transition: box-shadow 0.3s ease-in-out;
}

.popular-food__item:hover {
  box-shadow: 0 0 0 1px #ff6838;
}

.popular-food__card {
  max-width: 210px;
  min-height: 299px;
  height: 100%;
  padding: 20px 9px 22px;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
}

.popular-food__link {
  flex-grow: 1;
  margin-bottom: 16px;
}

.popular-food__img {
  margin: 0 auto;
  object-fit: contain;
  margin-bottom: 10px;
}

.popular-food__alt {
  padding: 0;
  word-spacing: 1px;
  font-weight: 400;
  font-size: 15px;
  line-height: 1;
}

.popular-food__price {
  font-weight: 500;
  font-size: 15px;
  line-height: 1.4;
  margin-bottom: 10px;
}

.popular-food__button {
  margin: 0 auto;
  padding: 6px 16px;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.3;
}

.mixitup-control-active {
  color: #fff;
  background-color: #ff6838;
}

.mixitup-control-active .popular__icon {
  fill: #fff;
}

.delivery__inner {
  max-width: 1030px;
  min-height: 585px;
  background-repeat: no-repeat;
  background-position: left center;
  background-size: contain;
  margin: 0 auto;
  display: flex;
  justify-content: flex-end;
}

.delivery__content {
  padding-top: 58px;
  max-width: 454px;
  margin-right: 15px;
}

.delivery__title {
  text-align: left;
  margin-bottom: 30px;
}

.delivery__text {
  word-spacing: -1px;
  margin-bottom: 28px;
}

.delivery__list {
  margin-bottom: 37px;
}

.delivery__item {
  font-size: 24px;
  height: 36px;
  display: flex;
  align-items: center;
  color: #363853;
  padding-left: 66px;
  background-repeat: no-repeat;
}

.delivery__item:not(:last-child) {
  margin-bottom: 20px;
}

.delivery__button {
  font-size: 18px;
  line-height: 1.3;
  padding: 12px 33px;
}

/* Slider */

.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.slick-track:before,
.slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir=rtl] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

.restaurant__title {
  padding-right: 18px;
  line-height: 1.3;
  margin-bottom: 43px;
}

.restaurant__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-items: center;
  grid-gap: 31px;
}

.restaurant__card {
  max-width: 370px;
  border-radius: 10px;
  box-shadow: 0 3px 3px rgba(0, 0, 0, 0.1019607843);
  background-color: #fff;
  transition: box-shadow 0.3s ease-in-out;
  position: relative;
}

.restaurant__card:hover {
  box-shadow: 0 3px 15px rgba(255, 104, 56, 0.7019607843);
}

.restaurant__img {
  border-radius: 10px 10px 0 0;
  object-fit: cover;
}

.restaurant__time {
  padding-left: 32px;
  background-repeat: no-repeat;
  background-position: left;
  background-size: contain;
}

.restaurant__link {
  position: absolute;
  right: 20px;
  bottom: 19px;
  padding: 4px 27px;
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.restaurant__link:hover .restaurant__icon {
  fill: #ff6838;
  transform: translateX(17px);
}

.restaurant__link:focus .restaurant__icon {
  fill: #ff6838;
}

.restaurant__link:active .restaurant__icon {
  fill: #ff6838;
}

.restaurant__icon {
  fill: #fff;
  transition: fill 0.3s, transform 0.3s ease-in-out;
}

.restaurant__info {
  padding: 15px 20px 20px;
}

.restaurant__name {
  font-weight: 400;
  font-size: 24px;
  line-height: 1.3;
  margin-bottom: 20px;
}

.restaurant__time {
  font-weight: 300;
  font-size: 18px;
  line-height: 30px;
  opacity: 0.9;
}

.restaurant .slick-dots {
  max-width: 165px;
  height: 15px;
  display: flex;
  justify-content: center;
  margin: 0 auto;
}

.restaurant .slick-dots li {
  display: flex;
  justify-content: center;
  align-items: center;
}

.restaurant .slick-dots li:not(:last-child) {
  margin-right: 15px;
}

.restaurant .slick-dots li button {
  font-size: 0;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: #c2c2c2;
  transition: background-color 0.3s, box-shadow 0.3s ease-in-out;
}

.restaurant .slick-dots li button:hover {
  background-color: #fff2ed;
  box-shadow: 0 0 0 2px rgba(255, 122, 80, 0.7019607843);
}

.restaurant .slick-dots li button:focus {
  background-color: #c2c2c2;
  box-shadow: 0 0 0 2px #ff6838;
}

.restaurant .slick-dots li button:active {
  background-color: #ffae94;
  box-shadow: none;
}

.restaurant .slick-dots .slick-active button {
  background-color: #ff6838;
}

