.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

.reviews__title {
  padding-top: 3px;
  margin-bottom: 45px;
}

.reviews-slider__item {
  max-width: 991px;
  display: flex !important;
  align-items: center;
  margin: 0 auto;
  margin-bottom: 49px;
}

.reviews-slider__photo {
  object-fit: cover;
  border-radius: 50%;
  margin-right: 40px;
}

.reviews-slider__quotes {
  max-width: 650px;
  padding-left: 39px;
}

.reviews-slider__comment {
  font-style: italic;
  margin-bottom: 30px;
  padding-right: 40px;
  position: relative;
}

.reviews-slider__comment::before {
  position: absolute;
  content: "";
  width: 24px;
  height: 24px;
  background-image: url(../img/svg/stack/sprite.svg#quotes);
  background-repeat: no-repeat;
  background-size: contain;
  left: -37px;
}

.reviews-slider__comment::after {
  position: absolute;
  content: "";
  width: 24px;
  height: 24px;
  background-image: url(../img/svg/stack/sprite.svg#quotes);
  background-repeat: no-repeat;
  background-size: contain;
  right: 2px;
  bottom: -1px;
  transform: rotateY(180deg);
}

.reviews-slider__name {
  font-weight: 500;
  line-height: 1.3;
  color: #ff6838;
}

.reviews-slider__prof {
  padding-top: 15px;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.3;
  color: #31352b;
  display: block;
}

.reviews-slider .slick-dots {
  max-width: 219px;
  height: 34px;
  display: flex;
  justify-content: center;
  margin: 0 auto;
}

.reviews-slider .slick-dots li {
  display: flex;
  justify-content: center;
  align-items: center;
}

.reviews-slider .slick-dots li:not(:last-child) {
  margin-right: 15px;
}

.reviews-slider .slick-dots li button {
  font-size: 0;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: #c2c2c2;
  transition: background-color 0.3s, box-shadow 0.3s ease-in-out;
}

.reviews-slider .slick-dots li button:hover {
  background-color: #fff2ed;
  box-shadow: 0 0 0 2px rgba(255, 122, 80, 0.7019607843);
}

.reviews-slider .slick-dots li button:focus {
  background-color: #c2c2c2;
  box-shadow: 0 0 0 2px #ff6838;
}

.reviews-slider .slick-dots li button:active {
  background-color: #ffae94;
  box-shadow: none;
}

.reviews-slider .slick-dots .slick-active button {
  background-color: #ff6838;
}

.reviews-slider .slick-arrow {
  position: absolute;
  bottom: 0;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid #c2c2c2;
  transition: box-shadow 0.3s, border 0.3s, background-color 0.3s ease-in-out;
}

.reviews-slider .slick-arrow:hover {
  background-color: #fff2ed;
  border: 2px solid #ffae94;
}

.reviews-slider .slick-arrow:focus {
  border: 2px solid #c2c2c2;
  box-shadow: 0 0 0 2px rgba(255, 104, 56, 0.7019607843);
  background-color: #fff;
}

.reviews-slider .slick-arrow:active {
  border: 2px solid transparent;
  background-color: #fff2ed;
  box-shadow: 0 0 0 2px transparent;
}

.reviews-slider .slick-arrow__prev {
  width: 34px;
  height: 34px;
  border-radius: 50%;
  left: 50%;
  transform: translateX(-325%);
}

.reviews-slider .slick-arrow__prev:hover .slick-arrow__icon {
  fill: #ffae94;
  transform: matrix(0, 1, 1, 0, 0, 0);
}

.reviews-slider .slick-arrow__prev:focus .slick-arrow__icon {
  fill: #c2c2c2;
  transform: matrix(0, 1, 1, 0, 0, 0);
}

.reviews-slider .slick-arrow__prev:active .slick-arrow__icon {
  fill: #ff6838;
  transform: matrix(0, 1, 1, 0, 0, 0);
}

.reviews-slider .slick-arrow__next {
  width: 34px;
  height: 34px;
  border-radius: 50%;
  right: 50%;
  transform: translateX(325%);
}

.reviews-slider .slick-arrow__next .slick-arrow__icon {
  transform: rotateY(180deg);
}

.reviews-slider .slick-arrow__next:hover .slick-arrow__icon {
  fill: #ffae94;
  transform: matrix(0, -1, -1, 0, 0, 0);
}

.reviews-slider .slick-arrow__next:focus .slick-arrow__icon {
  fill: #c2c2c2;
  transform: matrix(0, -1, -1, 0, 0, 0);
}

.reviews-slider .slick-arrow__next:active .slick-arrow__icon {
  fill: #ff6838;
  transform: matrix(0, -1, -1, 0, 0, 0);
}

.reviews-slider .slick-arrow__icon {
  width: 34px;
  height: 34px;
  fill: #c2c2c2;
  transition: transform 0.3s, fill 0.3s, stroke 0.3s ease-in-out;
}

.apps__inner {
  min-height: 381px;
