  align-items: center;
  background-repeat: no-repeat;
  background-position: center right;
}

.apps__content {
  max-width: 540px;
}

.apps__title {
  padding-top: 3px;
  text-align: left;
  margin-bottom: 45px;
}

.apps__text {
  margin-bottom: 30px;
}

.apps__list {
  display: flex;
}

.apps__item {
  height: 40px;
}

.apps__item:nth-child(1) .apps__link {
