  font-size: 14px;
  line-height: 1.3;
  color: #363853;
  border-radius: 5px;
  background-color: #fff;
  box-shadow: 0 0 0 1px #c2c2c2;
  padding: 13px 5px 13px 15px;
  margin-bottom: 12px;
  transition: color 0.3s, box-shadow 0.3s ease-in-out;
}

.footer__input::placeholder {
  color: #c2c2c2;
  transition: color 0.3s ease-in-out;
}

.footer__input:hover::placeholder {
  color: #363853;
}

.footer__input:focus {
  box-shadow: 0 0 0 2px #ff6838;
}

.footer__input:active::placeholder {
  color: #c2c2c2;
}

.footer__btn {
  padding: 13px 0;
  font-size: 14px;
  line-height: 1.3;
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer__btn:hover .footer__icon {
  fill: #ff6838;
}

.footer__btn:focus .footer__icon {
  fill: #ff6838;
}

.footer__icon {
  fill: #fff;
  margin-right: 15px;
  transition: fill 0.3s ease-in-out;
}

.footer__item {
  margin-bottom: 11px;
}

.footer__link {
  white-space: nowrap;
  font-weight: 400;
  font-size: 18px;
  line-height: 1;
  letter-spacing: 0.01em;
  vertical-align: middle;
}

.footer__copy {
  border-top: 1px solid #c2c2c2;
  padding: 30px 0;
  text-align: center;
  font-size: 14px;
}

.contact__item {
  margin-bottom: 11px;
}

.contact__link {
  display: inline-flex;
  align-items: center;
  font-style: normal;
  font-size: 16px;
  line-height: 2;
}

.contact__link:hover .contact__icon {
  stroke: #ff6838;
}

.contact__link:active .contact__icon {
  filter: drop-shadow(2px 2px 5px rgba(49, 53, 43, 0.5019607843));
}

.contact__icon {
  stroke: #31352b;
  margin-right: 7px;
  transition: stroke 0.3s ease-in-out;
}

.social__list {
  display: flex;
}

.social__item:not(:last-child) {
  margin-right: 13px;
}

.social__link {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 39px;
  height: 39px;
  border-radius: 50%;
  background-color: #ececec;
  transition: background-color 0.3s, box-shadow 0.3s ease-in-out;
}

.social__link:hover {
  background-color: #ff6838;
}

.social__link:hover .social__icon {
  background-color: #fff;
}

.social__link:focus {
  background-color: #fff2ed;
  box-shadow: 0px 0px 0px 2px rgba(255, 104, 56, 0.7019607843);
}

.social__link:focus .social__icon {
  background-color: #31352b;
}

.social__link:active {
  background-color: #fff2ed;
  box-shadow: 0px 0px 0px 2px transparent;
}

.social__link:active .social__icon {
  background-color: #ff6838;
}

.social__icon {
  display: inline-block;
  background-color: #31352b;
  mask-size: cover;
  -webkit-mask-size: cover;
  mask-position: center;
  -webkit-mask-position: center;
  mask-repeat: no-repeat;
  -webkit-mask-repeat: no-repeat;
  transition: background-color 0.3s ease-in-out;
}

